#app4 {
  display: flex;
  justify-content: center;
  align-items: center;
}
@keyframes change {
  0% {
    background: red;
  }
  100% {
    background: blue;
  }
}
#app4 .circle {
  width: 15vw;
  height: 15vw;
  border-radius: 50%;
  border: 1px solid red;
}

#app4 .circle.active {
  animation: change 1s infinite alternate linear;
}
